.heart-shape {
	position: relative;
	width: 100px;
	-webkit-animation: heart-beats 2s infinite;
  animation: heart-beats 2s infinite;
  margin: auto;
  top: 300px;
  /* margin-top: 45%; */ 
}

.heart-shape:before,
.heart-shape:after {
   background: white;
   position: absolute;
   width: 44px;
   height: 70px;
   top: 0;
   left: 44px;
   content: '';
   border-radius: 50px 50px 0 0;
   transform: rotate(-45deg) translateZ(0);
   -webkit-transform: rotate(-45deg) translateZ(0);
   transform-origin: 0 100%;
   -webkit-transform-origin: 0 100%;
}
 
.heart-shape:after {
   left: 0;
   transform: rotate(45deg) translateZ(0);
   -webkit-transform: rotate(45deg) translateZ(0);
   transform-origin :100% 100%;
   -webkit-transform-origin: 100% 100%;
}

 @keyframes heart-beats {
  0%   { -webkit-transform: scale(1); transform: scale(1); background-color:black;}
  7%  { -webkit-transform: scale(1.2); transform: scale(1.2); }
  12%  { -webkit-transform: scale(1.1); transform: scale(1.1); }
  20%  { -webkit-transform: scale(1.3); transform: scale(1.3); }
  60%  { -webkit-transform: scale(1); transform: scale(1); }
  100% { -webkit-transform: scale(0.99); transform: scale(0.99); }
}

@-webkit-keyframes heart-beats {
  0%  { -webkit-transform: scale(1); }
  7%  { -webkit-transform: scale(1.2); }
  12% { -webkit-transform: scale(1.1); }
  20% { -webkit-transform: scale(1.3); }
  60% { -webkit-transform: scale(1); }
  100% { -webkit-transform: scale(0.99); }
} 
